import * as React from "react";
import Link from "../Link";

const Paginator = (props) => {
    const { curPageNum, numOfPages } = props;
    if (numOfPages <= 1) {
        return null;
    }

    const prevUrl = curPageNum === 2 ? '/' : `/blogs/archive/${curPageNum - 2}`;
    const nextUrl = curPageNum === numOfPages ? '/' : `/blogs/archive/${curPageNum}`;
    return (
        <div>
            <Link href={prevUrl} disabled={curPageNum === 1}>Prev</Link>
            <Link href={nextUrl} disabled={curPageNum === numOfPages}>Next</Link>
        </div>
    );
}

export default Paginator;